.htag {
    margin-left: 3rem;
}

.roles {
    margin-left: 3rem;
    font-style: oblique;
}
.experience_container {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.f_icon{
    margin-right: 0.75rem;
    color: var(--color-primary);
}

.dates {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.date_info {
    margin-right: 5rem;
    padding-right: 5rem;
}

.broad {
    margin-left: 5rem;
    display: grid;
}

@media screen and (max-width: 1024px) {
    .experience_container{
        grid-template-columns: 1fr 1fr;

    }
    .experience_container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto 2;
        grid-template-columns: 1fr 1fr;
        
    }
}

@media screen and (max-width: 600px) {
    .experience_container{
        grid-template-columns: 1fr 1fr;
        padding: 0rem;
        margin: 0rem;
    }
    .experience_container > div{
        width: 100%;
        padding: 0rem;
        margin: 0rem;
    }

    .experience {
        margin-top: 0rem;
        padding-top: 0rem;
        margin-left: 0rem;
        padding: 0rem;
    }

    .broad {
        font-size: 12px;
        margin-left: 0rem;
        text-size-adjust: 50px;
        white-space: initial;
        word-wrap: break-word;
        padding: 0rem;
    }

    .date_info {
        margin-left: 3rem;
        padding-left: 3rem;
    }
    
    .roles {
        margin-left: 0.50rem;
        padding: 0.25rem;
        white-space: initial;
        word-wrap: break-all;
    }
    
    .dates {
        display: flex;
        margin-left: 2rem;
        white-space: initial;
    }

    .htag {
        margin-left: 1rem;
    }

    .tasma{
        margin-left: 0rem;
        white-space: initial;
        word-wrap: break-all;
        display: inline-block;
    }

    .th{
        word-break: 30ch;
    }
    
}